#inventory{
	width: 100vw;
	height: 100vh;
	position: absolute;
	top: 0;
	background: rgba(25, 25, 25, .8);
}

.inventory-inner-container{
	width: 200px;
	height: auto;

	margin: 50px;
	padding: 50px;

	background: #FFF;
	position: absolute;
	top: calc(40% - 100px);
	left: calc(50% - 175px);
}